import define1 from "./450051d7f1174df8@201.js";

export default function define(runtime, observer) {
  const main = runtime.module();
  main.variable(observer()).define(["md"], function(md){return(
md`# Fan Chart - Gujarat`
)});
  main.variable(observer()).define(["interval","d3","status"], function(interval,d3,status)
{
  interval.on("mouseover.highlight", function(d) {
    d3.select(this)
      .raise()
      .style("fill-opacity", 0.3);
    
    d3.select(status).text("highlight: " + d.letter);
  });
  
  interval.on("mouseout.highlight", function(d) {
    d3.select(this)
      .style("fill-opacity", 0.2);
    
    d3.select(status).text("highlight: none");
  });
}
);
  main.variable(observer("chart")).define("chart", ["d3","width","height","xAxis","yAxis","grid","area","data","x","y","line_smooth","line_preds","bpoint","mutable lookup","bisect","d3Legend"], function(d3,width,height,xAxis,yAxis,grid,area,data,x,y,line_smooth,line_preds,bpoint,$0,bisect,d3Legend)
{
  const svg = d3.create("svg")
      .attr("viewBox", [0, 0, width, height])
      .attr("font-family", "sans-serif")
      .attr("font-size", 10)
      .attr("stroke-miterlimit", 1);

  svg.append("g")
      .call(xAxis);

  svg.append("g")
      .call(yAxis);

  svg.append("g")
      .call(grid);

  svg.append("path")
      .attr("fill", "steelblue")
      .attr("fill-opacity", 0.2)
      .attr("d", area(data))
      .attr("id", "interval");
  
  svg.append("g")
      .attr("stroke", "orange")
      .attr("stroke-width", 1.5)
      .attr("fill", "orange")
    .selectAll("circle")
    .data(data.slice(0, data.length-75))
    .join("circle")
      .attr("cx", d => x(d.date))
      .attr("cy", d => y(d.raw))
      .attr("r", 2);
  
  svg.append("path")
      .attr("fill", "none")
      .attr("stroke", "green")
      .attr("stroke-width", 3)
      .attr("d", line_smooth(data.slice(0, data.length-75)));

  svg.append("path")
      .attr("fill", "none")
      .attr("stroke", "steelblue")
      .attr("stroke-width", 3)
      .attr("d", line_preds(data));
  
  svg.append("rect")
     .attr("width", width - x(data[bpoint].date))
     .attr("height", height)
     .attr("fill", "red")
     .attr("fill-opacity", 0.1)
     .attr("x", x(data[bpoint].date));

  
  if (!$0.value) $0.value = new Date(data[0].date);
  
  const bar = svg
    .append("line")
    .attr("style", "stroke-width:1")
    .attr("y2", height)
    .attr("x1", x($0.value))
    .attr("stroke", "darkblue")
    .attr("x2", x($0.value));
  
  const date_l = svg
    .append("text")
    .style("font", "18px sans-serif")
    .style('fill', 'darkblue')
    .attr("x", 10)
    .attr("y", 160);
  
  const predicted_l = svg
    .append("text")
    .style("font", "18px sans-serif")
    .style('fill', 'steelblue') 
    .attr("x", 10)
    .attr("y", 180);
  
  const actual_l = svg
    .append("text")
    .style("font", "18px sans-serif")
    .style('fill', 'green')
    .attr("x", 10)
    .attr("y", 200);  
  
  const formatTime = d3.utcFormat("%d/%m/%Y");
  
  function update(date) {
    const i = bisect.right(data, date);
    if (i < data.length && i > 0) {
      date_l.text(`${formatTime(data[i].date)}`);
      predicted_l.text(`Predicted: ${Math.round(data[i].avgpreds)}`);
      actual_l.text(`Actual: ${Math.round(data[i].smooth)}`);
      date_l.attr("x", (i > data.length/2)? x(data[i].date)-120 : x(data[i].date))
      actual_l.attr("x", (i > data.length/2)? x(data[i].date)-120 : x(data[i].date))
      predicted_l.attr("x", (i > data.length/2)? x(data[i].date)-120 : x(data[i].date))
    }
    $0.value = new Date(date);
    bar.attr("x1", x($0.value)).attr("x2", x($0.value));
  }
  
  svg.on("mousemove click touchmove", function() {
    const m = d3.mouse(this);
    update(x.invert(m[0]));
  });
  
  update($0.value);

//   svg.append("circle")
//       .attr("cx", x(observed.date))
//       .attr("cy", y(observed.mean))
//       .attr("r", 2.5);

//   svg.append("text")
//       .attr("x", x(observed.date))
//       .attr("y", y(observed.mean))
//       .attr("dx", 6)
//       .attr("dy", "0.35em")
//       .text(observed.mean.toLocaleString("en"));

//   svg.append("text")
//       .attr("x", x(observed.date))
//       .attr("y", y(observed.mean))
//       .attr("dx", 6)
//       .attr("dy", "1.35em")
//       .text(d3.utcFormat("%B %-d")(observed.date));
  var scale = d3.scaleOrdinal()
  .domain(["raw data", "actual deaths", "predicted deaths", "prediction area"])
  .range(["orange", "green", "steelblue", "red"]);
  
  var legend_line = d3Legend.legendColor()
  .shape("path", d3.symbol().type(d3.symbolSquare).size(150)())
  // Try commenting shapePadding() to see the difference
  .shapePadding(25) // cell padding
  .labelOffset(20)  // label offset from the shape
  .scale(scale);
 
  svg.append("g")
  .attr("class", "legend legend--ordinal")
  .attr("transform", "translate(50,50)");

  svg.select(".legend")
  .call(legend_line);

  return svg.node();
}
);
  main.define("initial bpoint", function(){return(
105
)});
  main.variable(observer("mutable bpoint")).define("mutable bpoint", ["Mutable", "initial bpoint"], (M, _) => new M(_));
  main.variable(observer("bpoint")).define("bpoint", ["mutable bpoint"], _ => _.generator);
  main.variable(observer("slider")).define("slider", ["html","mutable bpoint"], function(html,$0)
{
  const input = html`<input type="range" min="90" max="113" step="1" value=90" />`
  input.oninput = () => $0.value = input.valueAsNumber;
  return input;
}
);
  main.variable(observer("bisect")).define("bisect", ["d3"], function(d3){return(
d3.bisector(d => d.date)
)});
  main.define("initial lookup", function(){return(
null
)});
  main.variable(observer("mutable lookup")).define("mutable lookup", ["Mutable", "initial lookup"], (M, _) => new M(_));
  main.variable(observer("lookup")).define("lookup", ["mutable lookup"], _ => _.generator);
  main.variable(observer("city")).define("city", function(){return(
"Gujarat"
)});
  main.variable(observer("data")).define("data", ["d3","city","bpoint"], async function(d3,city,bpoint){return(
Object.assign(await d3.csv(`https://raw.githubusercontent.com/anag004/covid-model/master/projections/${city}_${bpoint}.csv`, d3.autoType))
)});
  main.variable(observer("line_smooth")).define("line_smooth", ["d3","x","y"], function(d3,x,y){return(
d3.line()
     .x(d => x(d.date))
     .y(d => y(d.smooth))
)});
  main.variable(observer("line_preds")).define("line_preds", ["d3","x","y"], function(d3,x,y){return(
d3.line()
    .x(d => x(d.date))
    .y(d => y(d.avgpreds))
)});
  main.variable(observer("area")).define("area", ["d3","x","y"], function(d3,x,y){return(
d3.area()
    .x(d => x(d.date))
    .y0(d => y(d.minpreds))
    .y1(d => y(d.maxpreds))
)});
  main.variable(observer("x")).define("x", ["d3","data","margin","width"], function(d3,data,margin,width){return(
d3.scaleUtc()
    .domain(d3.extent(data, d => d.date))
    .rangeRound([margin.left, width - margin.right])
)});
  main.variable(observer("y")).define("y", ["d3","data","height","margin"], function(d3,data,height,margin){return(
d3.scaleLinear()
    .domain([1, d3.max(data, d => d.maxpreds)])
    .rangeRound([height - margin.bottom, margin.top])
    .clamp(true)
)});
  main.variable(observer("xAxis")).define("xAxis", ["height","margin","d3","x","width"], function(height,margin,d3,x,width){return(
g => g
    .attr("transform", `translate(0,${height - margin.bottom})`)
    .call(d3.axisBottom(x).ticks(width / 80))
    .call(g => g.select(".domain").remove())
)});
  main.variable(observer("yAxis")).define("yAxis", ["margin","d3","y"], function(margin,d3,y){return(
g => g
    .attr("transform", `translate(${margin.left},0)`)
    .call(d3.axisLeft(y).ticks(null, ",d"))
    .call(g => g.select(".domain").remove())
    .call(g => g.append("text")
        .attr("x", -margin.left)
        .attr("y", 10)
        .attr("fill", "currentColor")
        .attr("text-anchor", "start")
        .text("Deaths per day"))
)});
  main.variable(observer("grid")).define("grid", ["x","margin","height","y","width"], function(x,margin,height,y,width){return(
g => g
    .attr("stroke", "currentColor")
    .attr("stroke-opacity", 0.1)
    .call(g => g.append("g")
      .selectAll("line")
      .data(x.ticks())
      .join("line")
        .attr("x1", d => 0.5 + x(d))
        .attr("x2", d => 0.5 + x(d))
        .attr("y1", margin.top)
        .attr("y2", height - margin.bottom))
    .call(g => g.append("g")
      .selectAll("line")
      .data(y.ticks())
      .join("line")
        .attr("y1", d => 0.5 + y(d))
        .attr("y2", d => 0.5 + y(d))
        .attr("x1", margin.left)
        .attr("x2", width - margin.right))
)});
  main.variable(observer("interval")).define("interval", ["d3"], function(d3){return(
d3.select("svg").select("path#interval")
)});
  main.variable(observer("height")).define("height", function(){return(
600
)});
  main.variable(observer("margin")).define("margin", function(){return(
{top: 20, right: 30, bottom: 30, left: 40}
)});
  main.variable(observer("d3")).define("d3", ["require"], function(require){return(
require("d3@5")
)});
  const child1 = runtime.module(define1);
  main.import("Scrubber", child1);
  main.variable(observer("d3Legend")).define("d3Legend", ["require"], function(require){return(
require("d3-svg-legend")
)});
  return main;
}
